.App {
  height: 100vh;
  display: grid;
  grid-template-columns: 15rem 1fr;
  grid-template-rows: auto 1fr;
  grid-template-areas: "head head"
                       "nav main";
}

.App > header {
  grid-area: head;
  -webkit-box-shadow: 0px 2px 4px -1px rgba(0, 0, 0, 0.2), 0px 4px 5px 0px rgba(0, 0, 0, 0.14), 0px 1px 10px 0px rgba(0, 0, 0, 0.12);
          box-shadow: 0px 2px 4px -1px rgba(0, 0, 0, 0.2), 0px 4px 5px 0px rgba(0, 0, 0, 0.14), 0px 1px 10px 0px rgba(0, 0, 0, 0.12);
  position: relative;
  z-index: 2;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
}

.App > header > h1 {
  width: 15rem;
  padding: 1rem;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin: 0;
}

header input.search {
  min-width: 25em;
  font: inherit;
  font-size: 1.2em;
  background: rgba(0,0,0,0.04);
  padding: 11px 16px 11px 16px;
  border: none;
  outline: none;
  margin: 11px;
  -webkit-transition: all .25s;
  -o-transition: all .25s;
  transition: all .25s;
}

header input.search:focus {
  -webkit-box-shadow: 0 1px 1px rgba(0,0,0,0.24);
          box-shadow: 0 1px 1px rgba(0,0,0,0.24);
  background: white;
}

.App > main {
  max-width: 60em;
  padding: 16px;
  grid-area: main;
  max-height: 100%;
  overflow: scroll;
}

.App > nav {
  grid-area: nav;
  max-height: 100%;
  overflow: scroll;
  border-right: 1px solid #e4e4e4;
  background: white;
}

.App > nav ul {
  list-style: none;
  padding: 0;
}

.App > nav > ul {
  padding: 16px;
}

.App > nav li a {
  color: inherit;
  text-decoration: none;
  font-weight: 500;
}

.App > nav li a:hover {
  text-decoration: underline;
}

.App > nav li a:active {
  opacity: 0.5;
}

.App > nav > ul > li > a {
  font-size: 1.5rem;
  margin: 1.25em 0 .7em;
  display: block;
  margin-left: 4px;
}

.App > nav > ul ul a {
  margin: 8px 0;
  display: block;
}

.App > nav > ul ul {
  padding: 1px 0 2px 16px;
  border-left: 1px solid rgb(172, 171, 171);
}

.App > nav > ul > li:first-child > a {
  font-size: 1.5rem;
  margin: 0 0 .7em;
  margin-left: 4px;
  display: block;
}

main h3 {
  font-size: 1.25em;
  padding: 32px 0 16px;
}



